import { Component, Host, h, State } from '@stencil/core';
import 'remote-web-worker';
import { workerPath } from '../../workers/conversation.worker.js?worker';

@Component({
  tag: 'insiteco-feedback',
  styleUrl: 'insiteco-feedback.sass',
  shadow: true,
})
export class InsitecoFeedback {
  @State() isVisible: boolean = false;
  private worker: Worker;

  constructor() {
    
    this.worker = new Worker(workerPath, { type: 'classic' });
    this.worker.onmessage = this.handleWorkerResponse.bind(this);
  }

  toggleVisibility() {
    this.isVisible = !this.isVisible;
  }

  handleWorkerResponse(event: MessageEvent) {
    
    console.log('Response from worker:', event.data);
  }

  sendRequestToWorker() {
    this.worker.postMessage({
      action: 'sendRequest',
      payload: {
        url: '',
        token: ''  
      }
    });
  }

  render() {
    return (
      <Host>
        {this.isVisible ? (
          <div class="flex justify-center bg-white items-center h-[500px] fixed right-4 bottom-4 rounded-2xl">
            <div class="card shadow-2xl p-4 w-96 h-full">
              <div class="card-body p-1 h-full flex flex-col justify-between">
                <div class="flex flex-col flex-1">
                  <section class="w-full flex justify-end h-8 items-center">
                    <section class="w-fit cursor-pointer" onClick={() => this.toggleVisibility()}>
                      <svg class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="2766" width="20" height="20">
                        <path
                          d="M807.6288 550.8096H224.2048c-17.5104 0-30.72-16.7936-30.72-38.7072s13.3632-38.7072 30.72-38.7072h583.424c17.5104 0 30.72 16.7936 30.72 38.7072s-14.0288 38.7072-30.72 38.7072z"
                          p-id="2767"
                          fill="black"
                        ></path>
                        <path
                          d="M807.5776 554.0352H224.2048c-19.456 0-34.0992-18.0224-34.0992-41.9328s14.6432-41.9328 34.0992-41.9328h583.3728c19.456 0 34.0992 18.0224 34.0992 41.9328 0 23.1424-15.2576 41.9328-34.0992 41.9328zM224.2048 476.6208c-15.7696 0-27.648 15.36-27.648 35.4816s11.8784 35.4816 27.648 35.4816h583.3728c15.36 0 27.648-15.9232 27.648-35.4816s-11.8784-35.4816-27.648-35.4816z"
                          p-id="2768"
                          fill="black"
                        ></path>
                      </svg>
                    </section>
                  </section>
                  <section class="mt-2 flex-1 overflow-hidden">
                    <insiteco-chat class="w-full h-full" />
                  </section>
                </div>
                <div class="flex flex-col gap-2 mt-3">
                  <insiteco-input />
                </div>
              </div>
            </div>
          </div>
        ) : (
          <div class={'vertical-text'} onClick={() => this.toggleVisibility()}>
            <span>Feedback</span>
          </div>
        )}
      </Host>
    );
  }
}
